<template>
  <div>
    <div id="legal-shop-detail" :class="skins=='days'?'white':'white balck'">
      <div class="top flex bgf8">
        <div class="top-t flex bod_rc">
          <div class="logo ft18" v-if="info.name">{{(info.name).charAt(0)}}</div>
          <div>
            <div>{{info.name}}</div>
            <div>{{$t('fat.register_time')}}：{{info.create_time}}</div>
          </div>
        </div>
        <div class="top-b flex">
          <div>
            <div>{{info.total}}</div>
            <div>{{$t('fat.odtotal')}}</div>
          </div>
          <div>
            <div>{{info.thirtyDays}}</div>
            <div>{{$t('fat.odmonth')}}</div>
          </div>
          <div>
            <div>{{info.done}}</div>
            <div>{{$t('fat.odfinish')}}</div>
          </div>
          <div>
            <div>{{rate}}</div>
            <div>{{$t('fat.odrate')}}</div>
          </div>
        </div>
        <div class="submit flex">
          <div @click="fb">{{$t('fat.submit')}}</div>
        </div>
      </div>
      <div class="md flex bgf8">
        <div>
          <span>{{$t('fat.phone')}}</span>
          <img v-if="info.prove_mobile == 1" src="../assets/images/success.png" alt>
          <img v-else src="../assets/images/icon_error.png" alt>
        </div>
        <div>
          <span>{{$t('fat.realAuth')}}</span>
          <img v-if="info.prove_real == 1" src="../assets/images/success.png" alt>
          <img v-else src="../assets/images/icon_error.png" alt>
        </div>
        <div>
          <span>{{$t('fat.advAuth')}}</span>
          <img v-if="info.prove_level == 1" src="../assets/images/success.png" alt>
          <img v-else src="../assets/images/icon_error.png" alt>
        </div>
      </div>
      <div class="list bgf8">
        <div class="tab">
          <div class="flex">
            <div>{{$t('fat.type')}}：</div>
            <div>
              <span
                :class="{'now':filterPms.type == 'sell'}"
                @click="filterPms.type = 'sell';getList()"
              >{{$t('fat.mysell')}}</span>
              <span
                :class="{'now':filterPms.type == 'buy'}"
                @click="filterPms.type = 'buy';getList()"
              >{{$t('fat.mybuy')}}</span>
            </div>
          </div>
          <div class="flex">
            <div>{{$t('fat.status')}}：</div>
            <div>
              <span
                :class="{'now':filterPms.wasDone == false}"
                @click="filterPms.wasDone = false;getList()"
              >{{$t('td.nofinish')}}</span>
              <span
                :class="{'now':filterPms.wasDone == true}"
                @click="filterPms.wasDone = true;getList()"
              >{{$t('td.finished')}}</span>
            </div>
          </div>
          <div class="orders" @click="links">{{$t('fat.completeOrder')}}</div>
        </div>
        <div class="ul-head tc bdb">
          <div class="w10 tl">{{$t('td.currency')}}</div>
          <div class="w15">{{$t('td.num')}}</div>
          <div class="w25">{{$t('td.limit')}}</div>
          <div class="w15">{{$t('td.price')}}</div>
          <div class="w10">{{$t('td.method')}}</div>
          <div class="tr">{{$t('td.do')}}</div>
        </div>
        <ul :class="[showWhich+'-box']">
          <li
            v-for="(item,index) in list"
            :key="index"
            :class="[item.type == 'buy'?'buy-item':'sell-item']"
            class="bod_bc tc bdb"
          >
            <div class="w10 tl">{{item.currency_name}}</div>
            <div class="w15">{{item.surplus_number || '0.000' | numFilters(3)}}</div>
            <div
              class="w25"
            >{{item.min_number || '0.000' | numFilters(3)}}{{item.currency_name}}-{{item.max_number || '0.000' | numFilters(3)}}{{item.currency_name}}</div>
            <div class="w15">{{item.price || '0.000' | numFilters(3)}}</div>
            <div class="flex alcenter center tc w10">
              <img v-if="item.way == 'ali_pay'" src="../assets/images/zfb_icon.png">
              <img v-if="item.way == 'we_chat'" src="../assets/images/wx_icon.png">
              <img v-if="item.way == 'bank'" src="../assets/images/bank_icon.png">
            </div>
            <!-- <div class="w10">{{item.way_name}}</div> -->
            <div class="tr">
              <router-link tag="span" :to="{path:'/shopLegalRecord',query:{id:item.id}}">{{$t('fat.seeOrder')}}</router-link>
              <span @click="changeOrder('back_send',item.id)">{{$t('fat.withdraw')}}</span>
              <span @click="changeOrder('error_send',item.id)">{{$t('fat.abnormal')}}</span>
              <span @click="changeOrder('down_send',item.id)">{{$t('fat.lowershelf')}}</span>
            </div>
          </li>
        </ul>
        <!-- 分页 -->
        <el-pagination
          layout="prev, pager, next"
          v-if="total > 10"
          :total="total"
          @current-change="handleCurrentChange"
        ></el-pagination>
        <!-- <div class="more" @click="getList(true)" v-if="list.length">加载更多</div>
        <div class="more" v-else>{{$t('td.nomore')}}</div>-->
      </div>
    </div>
    <!-- 发布弹窗 -->
    <el-dialog title :visible.sync="dialogVisible" width="600px" center>
      <div class="transfer-content">
        <div class="transfer-list">
          <div class="tab bdb flex">
            <div>{{$t('fat.psType')}}：</div>
            <div
            class="mr20 ml20"
              :class="{'now':index==current}"
              v-for="(item,index) in typeList"
              :key="index"
              @click="changeType(index,item.type)"
            >{{item.name}}</div>
          </div>
          <div class="flex currency-name">
            <span class="lefts">{{$t('td.currency')}}：</span>
            <span>{{info.currency_name}}</span>
          </div>
          <div class="flex currency-name">
            <span class="lefts">{{$t('td.method')}}：</span>
            <el-select v-model="way" :placeholder="$t('fat.pselect')">
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              ></el-option>
            </el-select>
          </div>
          <div class="flex content-text mt5">
            <span class="lefts">{{$t('td.price')}}：</span>
            <input type="text" name id v-model="price">
          </div>
          <div class="flex  content-text">
            <span class="lefts">{{$t('td.num')}}：</span>
            <input type="text" v-model="total_number">
          </div>
          <div class="flex  content-text">
            <span class="lefts">{{$t('fat.minNum')}}：</span>
            <input type="text" v-model="min_number">
          </div>
          <div class="flex content-text">
            <span class="lefts">{{$t('fat.maxNum')}}：</span>
            <input type="text" v-model="max_number">
          </div>
        </div>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button  @click="fabu">{{$t('fat.submit')}}</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      current: 0,
      token: "",
      sellerId: "",
      info: { lists: { data: [] } },
      showWhich: "none",
      isShow: false,
      showDetail: false,
      detail: { money: "", num: "" },
      timer: "",
      rate: "--",
      filterPms: { id: "", page: 1, wasDone: '', type: "sell" },
      list: [],
      submitPms: { type: "sell" },
      typeList: [{ name: this.$t('td.sell'), type: "sell" }, { name: this.$t('fat.wantBuy'), type: "buy" }],
      type: "sell",
      way: '',
      price: "",
      total_number: "",
      min_number: "",
      max_number: "",
      currencyId: "",
      total: 0,
      dialogVisible: false,
      options:[
        {
          value: 'ali_pay',
          label: this.$t('fat.alipay')
        },
        {
          value: 'we_chat',
          label: this.$t('fat.wechat')
        },
        {
          value: 'bank',
          label: this.$t('fat.bank')
        }
      ],
      fabuBtn:true,
      cancelBtn:true,
      skins: localStorage.getItem("skin") || "days"
    };
  },
  created() {
    this.token = window.localStorage.getItem("token") || "";
    if (this.token) {
      this.sellerId = this.$route.query.id || "";
      this.getSellerInfo();
      this.getList();
    }
  },
  beforeCreate() {
    if (
      localStorage.getItem("skin") &&
      localStorage.getItem("skin") == "nights"
    ) {
      document
        .querySelector("html")
        .setAttribute("style", "background-color:#000;");
    } else {
      document
        .querySelector("html")
        .setAttribute("style", "background-color:#f8f6f6;");
    }
  },
  methods: {
    // 切换发布类型
    changeType(index, type) {
      this.current = index;
      this.type = type;
      this.way = '';
      this.price = '';
      this.total_number = '';
      this.min_number = '';
      this.max_number = '';

    },
    // 获取默认数据
    getSellerInfo(more) {
      this.showWhich = "none";
      if (!more) {
        this.page = 1;
      }
      let i = layer.load();
      this.$http({
        url: "/api/seller_info",
        params: {
          id: this.sellerId,
          page: 1
        },
        headers: { Authorization: this.token }
      }).then(res => {
        layer.close(i);
        if (res.data.type == "ok") {
          this.info = Object.assign({}, res.data.message);
          this.currencyId = res.data.message.currency_id;
          var result = this.$utils.filterDecimals(
            (res.data.message.done / res.data.message.total) *100,2);
          this.rate = result == "NaN" ? "0.00" : result + "%";
          this.currency_id = res.data.message.currency_id;
        }
      });
    },
    // 分页
    handleCurrentChange(val) {
      var that = this;
      that.filterPms.page = val;
      that.getList();
    },
    getList() {
      this.list = [];
      var pms = {};
      pms.page = this.filterPms.page;
      if (this.filterPms.type != "none") {
        pms.type = this.filterPms.type;
      }
      if (this.filterPms.wasDone != "none") {
        pms.was_done = this.filterPms.wasDone;
      }
      pms.id = this.sellerId;
      var i = layer.load();
      this.$http({
        url: "/api/seller_trade",
        params: pms,
        headers: { Authorization: this.token }
      }).then(res => {
        layer.close(i);
        if (res.data.type == "ok") {
          var msg = res.data.message;
          this.list = msg.data;
          this.total = msg.total;
        }
      });
    },
    changeOrder(url, id) {
      if(this.cancelBtn){
        this.cancelBtn = false;
        this.$http({
        url: "/api/" + url,
        method: "post",
        data: { id: id },
        headers: { Authorization: this.token }
      }).then(res => {
        this.cancelBtn = true;
        if (res.data.type == "ok") {
          layer.msg(res.data.message);
          this.getList();
          
        } else {
          layer.msg(res.data.message);
        }
      });
      }
      
    },
    setDetail(item) {
      this.detail = Object.assign({ which: "money", money: "", num: "" }, item);
      this.showDetail = true;
      var time = 60;
      var that = this;
      that.timer = setInterval(function() {
        time--;
        that.$refs.remainTime.innerHTML = time;
        if (time == 0) {
          that.showDetail = false;
          clearInterval(that.timer);
        }
      }, 1000);
    },
    buySell() {
      // this.detail = Object.assign({which:'money'},item)
      // var value = this.detail.which == 'money'?detail.money:detail.num;
      var value = "";
      if (this.detail.which == "money") {
        value = this.detail.money;
        if (value == "") {
          return;
        } else if (value - 0 - this.detail.limitation.min < 0) {
          layer.msg(this.$t('fat.notlow'));
          return;
        } else if (value - 0 - this.detail.limitation.max > 0) {
          layer.msg(this.$t('fat.nothigh'));
          return;
        }
      } else {
        value = this.detail.num;
        if (value == "") {
          return;
        } else if (value > this.detail.surplus_number) {
          layer.msg(this.$t('fat.notnum'));
          return;
        }
      }
      this.$http({
        url: "/api/do_legal_deal",
        method: "post",
        data: { means: this.detail.which, value: value, id: this.detail.id },
        headers: { Authorization: this.token }
      }).then(res => {
        this.showDetail = false;
        if (res.data.type == "ok") {
          var message = res.data.message;
          layer.msg(message.msg);
          if (this.detail.type == "sell") {
            this.$router.push({
              path: "/legalPay",
              query: { id: msg.data.id }
            });
          } else {
            this.$router.push({
              path: "/components/payCannel",
              query: { id: msg.data.id }
            });
          }
        }
      });
    },
    cancel() {
      clearInterval(this.timer);
      this.showDetail = false;
    },
    // 发布弹层
    fb() {
      this.dialogVisible = true;
      this.way = '';
      this.price = '';
      this.total_number = '';
      this.min_number = '';
      this.max_number = '';
      // this.current = 0;
    },
    // 弹层关闭
    close() {
      this.isShow = false;
    },
    // 发布
    fabu() {
      var type = this.type;
      var way = this.way;
      var price = this.price;
      var total_number = this.total_number;
      var min_number = this.min_number - 0;
      var max_number = this.max_number - 0;
      var currency_id = this.currency_id;
      if (way == '') {
        return layer.msg(this.$t('ctc.payway'));
      }
      if (!price || price <= 0) {
        return layer.msg(this.$t('ctc.pprice'));
      }
      if (!total_number || total_number <= 0) {
        return layer.msg(this.$t('miscro.enterQuantity'));
      }
      if (!min_number || min_number <= 0) {
        return layer.msg(this.$t('fat.pmin'));
      }
      if (!max_number || max_number <= 0) {
        return layer.msg(this.$t('fat.pmax'));
      }
      if (max_number < min_number) {
        return layer.msg(this.$t('fat.pnot'));
      }
      if(this.fabuBtn){
        this.fabuBtn = false;
        this.$http({
        url: "/api/" + "legal_send",
        method: "post",
        data: {
          type: this.type,
          way: this.way,
          price: this.price,
          total_number: this.total_number,
          min_number: min_number,
          max_number: max_number,
          currency_id: this.currency_id
        },
        headers: { Authorization: this.token }
      }).then(res => {
        this.fabuBtn = true;
        if (res.data.type == "ok") {
          this.dialogVisible = false;
          layer.msg(res.data.message);
          this.getSellerInfo();
          this.getList();
        } else {
          layer.msg(res.data.message);
        }
      });
      }
      
    },
    links() {
      var that = this;
      this.$router.push({
        path: "/legalDoneRecord",
        query: { id: that.currencyId }
      });
    }
  }
};
</script>

<style lang='scss' scoped>
 .transfer-list{
   .tab {
      position: relative;
      line-height: 40px;
      > .flex {
        > div:first-child {
          width: 80px;
        }
      }
      span {
        margin-right: 20px;
        cursor: pointer;
      }
      .now {
        border-bottom:2px solid #f0b90b;
      }
    }
 }
.white {
  margin: 66px auto 55px;
  width: 1200px;
  line-height: 30px;
  padding-bottom: 30px;
  background-color: #fff;
  -webkit-box-shadow: 0 0 29px 0 rgba(32, 36, 48, 0.08);
  box-shadow: 0 0 29px 0 rgba(32, 36, 48, 0.08);
  min-height: 620px;
  position: relative;
  top: 22px;

  > .top {
    // background: #f8f8f8;
    line-height: 36px;
    padding: 16px 30px;
    > .top-t {
      align-items: center;
      padding-right: 30px;
      // border-right: 1px solid #ccc;
      margin-right: 30px;
      > .logo {
        margin-right: 20px;
        border-radius: 50%;
        width: 50px;
        height: 50px;
        line-height: 50px;
        background: #f0b90b;
        text-align: center;
        color: #fff;
      }
    }
    > .top-b {
      > div {
        min-width: 100px;
        text-align: center;
        margin-right: 10px;
      }
    }

    > .submit {
      align-items: center;
      justify-content: flex-end;
      > div {
        border-radius: 2px;
        margin-left: 30px;
        color: #fff;
        padding: 0 16px;
        font-size: 14px;
        height: 30px;
        line-height: 30px;
        background: #f0b90b;
        cursor: pointer;
      }
    }
  }

  > .md {
    // background: #f8f8f8;
    padding: 16px 30px;
    > div {
      margin-right: 50px;
      img {
        width: 16px;
        height: 16px;
        position: relative;
        top: -1px;
      }
    }
  }
  > .list {
    // background: #f8f8f8;
    margin-top: 20px;
    padding: 5px 30px 16px;
    > .tab {
        > div {
          margin-right: 50px;
        }
        .now {
          border-bottom: 2px solid #f0b90b;
        }
        span{
          display: inline-block;
          margin-left: 10px;
          margin-right: 20px;
          padding-bottom: 3px;
          cursor: pointer;
        }
      }
    > .ul-head {
      height: 50px;
      > div {
        // text-align: center;
        float: left;
      }
      > div:last-child {
        float: right;
      }
    }
    > ul {
      li {
        padding: 16px 0;
        height: 62px;
        // border-bottom: 1px solid #ddd;
        > div {
          float: left;
        }
        > div:last-child {
          float: right;
        }
        > div:last-child {
          // text-align: right;
          // display: flex;
          height: 30px;
          span {
            float: right;
            background: #f0b90b;
            padding: 0 12px;
            border-radius: 2px;
            color: #fff;
            cursor: pointer;
            font-size: 12px;
            margin-right: 5px;
          }
        }
      }
    }
    > .buy-box > .sell-item,
    .sell-box > .buy-item {
      display: none;
    }
    .more {
      padding: 20px 20px;
      text-align: center;
      cursor: pointer;
    }
  }
  > .submit-box {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.8);
    > .content {
      margin: 100px auto 0;
      border-radius: 4px;
      width: 440px;
      padding: 20px 30px 26px 30px;
      background: #fff;
      line-height: 30px;
      position: relative;
      .close {
        position: absolute;
        top: 5px;
        right: 10px;
        cursor: pointer;
      }
      > .btn {
        background: #f0b90b;
        color: #fff;
        border-radius: 2px;
        text-align: center;
        line-height: 40px;
      }
      > .flex {
        // display: flex;
        margin-bottom: 20px;
        span {
          width: 130px;
        }
        input,
        select {
          border-radius: 2px;
          border: 1px solid #cad7e0;
          padding: 0 16px;
          flex: 1;
          background: #fff;
        }
      }
      > .tab {
        margin: 10px 0 20px;
        span {
          margin-right: 20px;
        }
        cursor: pointer;
        > .selected {
          font-weight: bold;
          color: #f0b90b;
        }
      }
      > .inp {
        display: flex;
        border-radius: 2px;
        justify-content: space-between;
        border: 1px solid #1b2a3e;
        padding: 3px 16px;
        span {
          padding-left: 10px;
          margin-left: 10px;
        }
        .all {
          border-left: 1px solid #1b2a3e;
          font-weight: 600;
          cursor: pointer;
        }
      }
      > .tip {
        font-size: 12px;
        margin-top: 10px 16px;
        color: #f0b90b;
      }
      > .btns {
        justify-content: space-between;

        > div {
          width: 46%;
          text-align: center;
          border-radius: 2px;
          line-height: 40px;
          cursor: pointer;
        }
        > .cancel {
          background: #ccc;
        }
        > .ok {
          background: #f0b90b;
          color: #fff;
        }
      }
    }
  }
}
.orders {
  position: absolute;
  top: 20px;
  right: 0;
  color: #f0b90b;
  cursor: pointer;
}
.el-pagination {
  text-align: center;
  margin-top: 30px;
}
.el-pagination .btn-prev,
.el-pagination .btn-next {
  display: none;
}
.el-pager .active{
  color: #f0b90b;
}
.currency-name{
  line-height: 40px;
}
.lefts{
  width:110px;

}
.el-select-dropdown{
  background-color: #fff !important;
    border: 1px solid #fff !important;
}
.el-popper[x-placement^=bottom] .popper__arrow::after{
  border-bottom-color: #fff !important;
}
.content-text{
  line-height: 50px;
}
.content-text input{
  width: 250px;
  border: 1px solid #eee;
  line-height: 40px;
  height: 40px;
  margin-top: 5px;
  padding: 0 15px;
  border-radius: 4px;
}
.mt5{
  margin-top: 5px;
}
.el-select{
  width: 250px;
}
.dialog-footer button{
  width: 300px;
  background-color: #f0b90b;
  border: 1px solid #f0b90b;
  color:#fff;

}
.el-button:focus, .el-button:hover{
  background-color: #f0b90b;
  border: 1px solid #f0b90b;
    color:#fff;
}
.balck{
  background-color: #1e2b34;
  color: #fff;
}
.balck .bdb{
  border-bottom: 1px solid #252323;
}
</style>



